@font-face{
  font-family: Bebas Neue;
  font-stretch: expanded;
  src:"../public/resources/fonts/BebasNeue-Regular.ttf";
}
html{
  overflow: hidden;
}
.App{
  width: 100vw;
  height: 100vh;
  background-color: rgb(7, 7, 7);
  color: white;
  overflow: hidden;
  display: flex;
  font-family: Bebas Neue, cursive;
  font-weight: lighter;
}
.nav-bar{
  width: 1vw;
  height: 100vh;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  z-index: 99;
  background-color: rgb(7, 7, 7);
}
.nav-icon{
  width: 1.5rem;
  transition: ease-in-out 0.2s;
  margin-top: 2rem;
  margin-bottom: 2rem;
}
.nav-icon:hover{
  transform: translateY(-20%);
  transition: ease-in-out 0.2s;
}
.selected{
  height: 1px;
  background-color: red;
  transition: all ease-in-out 0.4s;
}
