.home-page{
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.home-page *{
    z-index: 1;
}
.home-page-title-container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.home-page-title{
    opacity: 50%;
    font-size: 5rem;
    display: inline-block;
    line-height: 0;
}
.home-page-title-logo{
    float: right;
    width: 25rem;
    margin-left: 2rem;
}
.home-page-description{
    color: var(--empty-color);
    font-size: 2.25rem;
    background-color: rgba(0, 0, 0, 0.25);
    padding: 1rem;
    width: 80%;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    text-align: center;
}
.home-page-description span{
    display:inline-block;
}
.home-page-description img{
    float: right;
    width: 1rem;
    height: 1rem;
    border-bottom: 1px solid rgba(255, 0, 0, 0.609);
    padding: 0.5rem;
}
.dev-links{
    width: 20rem;
    display: flex;
    justify-content: right;
    align-items: center;
    padding: 1rem;
    position: absolute;
    bottom: 0;
    right: 0;
}
.dev-links img{
    width: 2rem;
}
.home-page-background{
    clear:both;
    background-image: url("background.svg");
    background-size: 5%;
    display: block;
    width: 100vw;
    height: 100vh;
    opacity: 0.02;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 0;
}