.search-page{
    width: 100vw;
    height: 100vh;
  }
  .search-page-title{
    color: rgb(165, 165, 165);
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    margin-top: 8rem;
    font-size: 2rem;
  }
  .mahadflix-logo{
    width: 8rem;
    min-width: 8rem;
    position: absolute;
    padding: 1rem;
    left: 4vw;
  }
  
  body{
    margin: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  .search-icon{
    z-index: 3;
    opacity: 0.5;
    margin-left: 1.5rem;
  }
  .search-bar-container{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .search-bar{
    margin-left: -2rem;
    width: 50%;
    min-width: 20rem;
    border-radius:  10rem;
    border: 1px solid rgba(0, 0, 0, 0.1);
    outline: none;
    box-shadow: 5px 5px 5px rgba(0, 0, 0, 0.1);
    height: 2rem;
    font-size: 1.5rem;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color:rgba(0, 0, 0, 0.5);
    padding-left: 2.5vw;
    padding-bottom: 0.25rem;
  }
  .gradient{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 5;
    background: linear-gradient(180deg, rgba(0,0,0,0) 55%, rgba(0,0,0,1) 95%);
    clear: none;
    pointer-events: none;
  }
  .search-results{
    margin: 0.5rem;
    width: 100%;
    height: 40vh;
    /*border: 2px solid white;*/
    margin: 0 auto;
    padding: 0.5rem;
    padding-bottom: 20vw;
    overflow-y: scroll;
    overflow-x: hidden;
    position: relative;
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    -ms-overflow-style: none;  /* IE and Edge */
    scrollbar-width: none;  /* Firefox */
    background: rgb(10, 10, 10);
  }
  .search-results::-webkit-scrollbar {
    display: none;
  }
  .movie *{
    margin: 0;
  }
  .movie{
    overflow: hidden;
    flex-basis: 15.5%;
    height: 22.5vw;
    margin-left: 0.5%;
    margin-right: 0.5%;
    margin-bottom: 2rem;
    text-align: left;
    z-index: 4;
    cursor: pointer;
    transition: all 0.2s ease-in-out;
    text-shadow: 3px 3px solid #00000030;
    display: flex;
    flex-direction: column;
  }
  .movie:hover{
    transform: scale(1.05);
    transition: all 0.3s ease-in-out;
  }
  .movie-title{
    font-size: 1.5vw;
    padding-left: 0.5rem;
    padding-top: 15%;
    width: 90%;
    position: relative;
    z-index: 3;
  }
  .movie-plot{
    background-color: rgba(0, 0, 0, 0.507);
    text-align: left;
    padding-left: 0.5rem;
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    position: relative;
    font-size: 2ch;
    z-index: 1;
    margin-top: 1rem;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
    font-size: 1rem;
  }
  .movie-wallpaper{
    width: 100%;
    height: 22.5vw;
    position: absolute;
    top: 0;
    left: 0;
    z-index: -1;
  }
  #alert-dialog-description{
    text-align: center;
    font-weight: bold;
  }
  .movie-info{
    width: 100%;
    padding: 0.5rem;
    display: flex;
    justify-content: flex-start;
    position: relative;
    z-index: 3;
    background-color: #00000050;
  }
  .movie-info *{
    opacity: 1;
    z-index: 3;
    font-size: 1.25rem;
    margin-right: 1rem;
  }
  .imdb-rating{
    background-color: black;
    border-radius: 1%;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  .content-rating{
    border: 1px solid whitesmoke;
    padding-left: 0.5rem;
    padding-right: 0.5rem;
  }
  .nominated{
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index: 4;
    font-size: 2rem;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .nominated img{
    width: 2rem;
  }
  .nominated span{
    display: flex;
    align-items: center;
    justify-content: center;
  }